import { toast } from "@zerodevx/svelte-toast";
export const debounce = (f, timeout = 250) => {
    let timer;
    return (...v) => {
        clearTimeout(timer);
        timer = setTimeout(() => f(...v), timeout);
    };
};
export const toastSuccess = (msg) => toast.push(msg, {
    theme: {
        "--toastBackground": "#48BB78",
        "--toastProgressBackground": "#2F855A",
    },
});
export const toastError = (msg) => toast.push(msg, {
    theme: {
        "--toastBackground": "#F56565",
        "--toastProgressBackground": "#C53030",
    },
});
export const measureTime = (f) => {
    let t0 = performance.now();
    f();
    let t1 = performance.now();
    return (t1 - t0).toFixed(3);
};
export const copyTextToClipboard = (text) => navigator.clipboard
    .writeText(text)
    .then(() => toastSuccess("Copied to clipboard"))
    .catch((e) => {
    console.error("clipboard.writeText failed", e);
    toastError("Could not access the clipboard");
});
